.projects {
  position: relative;
  z-index: 100;
  text-align: center;
  background-color: var(--color-black);
}

.divinest-light__intro {
  position: relative;
  width: 100%;
}

.divinest-light__bg {
  display: flex;
  width: 100%;
  height: 100%;
  background-image: url(./../img/divinest-light.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: var(--color-black);
}

.divinest-light__video {
  width: 100%;
  height: 100%;
}

.divinest-light__link {
  position: absolute;
  left: 50%;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 5px;
  border-radius: 20px;
  width: 480px;
  height: 90px;
  font-family: "Roboto Mono";
  font-weight: 400;
  font-size: 30px;
  line-height: 30px;
  text-transform: uppercase;
  color: var(--color-white);
  background-color: var(--color-purple);
  transform: translate(-50%, -50%);
  transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;
}

.divinest-light__link:hover {
  color: var(--color-purple);
  background-color: var(--color-white);
  transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;
}

.divinest-light__dsr {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 25px;
  padding-bottom: 70px;
}

.divinest-light__titile {
  margin-bottom: 35px;
  font-family: "Space Mono";
  font-weight: 700;
  font-style: italic;
  font-size: 32px;
  text-transform: uppercase;
  color: var(--color-white);
}

.divinest-light__text {
  max-width: 605px;
  line-height: 30px;
  color: var(--color-white);
}

.divinest-light__text:not(:last-child) {
  margin-bottom: 27px;
}

.projects-body {
  position: relative;
  padding-bottom: 100px;
  background-color: var(--color-dark);
}

.mobile-projects {
  display: flex;
  justify-content: space-between;
  margin-bottom: 140px;
  padding-top: 85px;
}

.game {
  width: 25%;
}

.game__link {
  display: block;
  width: 100%;
}

.game__link:hover .game__title {
  color: var(--color-purple);
  transition: color 0.3s ease-in-out;
}

.game__bg {
  display: flex;
  width: 100%;
  height: 100%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.game__video {
  width: 100%;
  height: 100%;
}

.game__text {
  padding-top: 30px;
  text-align: left;
}

.game__title {
  margin-bottom: 20px;
  font-family: "Space Mono";
  font-weight: 700;
  font-style: italic;
  font-size: 32px;
  text-transform: uppercase;
  color: var(--color-white);
  transition: color 0.3s ease-in-out;
}

.game__dsr {
  font-family: "Roboto Mono";
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  color: var(--color-white);
}

.game:not(:last-child) {
  margin-right: 20px;
}

.first-game__bg {
  background-image: url(./../img/astrofall.jpg);
}

.second-game__bg {
  background-image: url(./../img/spacetor.jpg);
}

.third-game__bg {
  background-image: url(./../img/hayseed.jpg);
}

.fourth-game__bg {
  background-image: url(./../img/cosmo-natty.jpg);
}

.last-game {
  display: flex;
}

.last-game__link:hover .last-game__title {
  color: var(--color-purple);
  transition: color 0.3s ease-in-out;
}

.last-game__body {
  display: flex;
}

.last-game__bg {
  display: flex;
  background-image: url(./../img/timeline.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.last-game__video {
  width: 100%;
  height: 100%;
}

.last-game__text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 20px;
  text-align: left;
}

.last-game__title {
  margin-bottom: 20px;
  font-family: "Space Mono";
  font-weight: 700;
  font-style: italic;
  font-size: 32px;
  text-transform: uppercase;
  color: var(--color-white);
  transition: color 0.3s ease-in-out;
}

.last-game__dsr {
  max-width: 335px;
  font-family: "Roboto Mono";
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  color: var(--color-white);
}

.footer {
  position: relative;
  padding-top: 50px;
  padding-bottom: 30px;
  background-color: var(--color-dark);
}

.footer__body {
  display: flex;
  justify-content: space-between;
}

.footer__email {
  color: var(--color-white);
}

.email {
  transition: color 0.3s ease-in-out;
}

.email:hover {
  color: var(--color-purple);
  transition: color 0.3s ease-in-out;
}

.media__list {
  display: flex;
  justify-content: space-between;
}

.media__item:not(:last-child) {
  margin-right: 40px;
}

.media__img {
  fill: var(--color-purple);
  transition: fill 0.3s ease-in-out;
}

.media__img:hover {
  fill: var(--color-white);
  transition: fill 0.3s ease-in-out;
}

.site-creator {
  padding-left: 60px;
  max-width: 290px;
  text-align: right;
  color: var(--color-white);
}

@media (max-width: 1440px) {
  .first__info {
    max-width: 950px;
  }
  .first__title {
    font-size: 60px;
    line-height: 60px;
  }
  .first__descr {
    max-width: 350px;
    font-size: 16px;
    line-height: 24px;
  }
}

@media (max-width: 1024px) {
  .first__container {
    padding-top: 25.5vh;
  }
  .first__info {
    align-items: center;
    max-width: 620px;
  }
  .first__descr {
    margin-right: 0;
    max-width: 450px;
  }
  .to-projects {
    margin-bottom: 130px;
    font-size: 16px;
  }
}

@media (max-width: 768px) {
  .first__info {
    max-width: 550px;
  }
  .first__title {
    margin-bottom: 10px;
    font-size: 50px;
    line-height: 55px;
  }
  .first__descr {
    max-width: 400px;
  }
}

@media (max-width: 576px) {
  .first__info {
    max-width: 310px;
  }
  .first__title {
    font-size: 27px;
    line-height: 36px;
  }
  .first__descr {
    width: 280px;
    font-size: 12px;
    line-height: 18px;
  }
  .to-projects {
    margin-bottom: 60px;
    font-size: 12px;
  }
  .to-projects__icon {
    height: 34px;
  }
}
